import React, { useEffect, useState } from 'react'
import { CustCheckbox, Input, Label } from '../../../commanComponent'
import { CustomButton, CustomError } from '../../../globalComponents'
import { useCustomTranslation } from '../../../../translation'
import { validateSchema } from '../../../../utils/ValidationHelper'
import CustomSelect from '../../../commanComponent/CustomSelect'
import { addNewKRA } from '../../../../utils/ValidationSchema'
import { financialYearDuration } from '../../../../utils/Constants.config'

const AddNewKRA = (props) => {
    const {
        openModel,
        handleOpenClose,
        allDepartmentList,
        financialYearListData,
        handleCreateUpdateKra
    } = props
    const { t } = useCustomTranslation()
    const [errors, setErrors] = useState('')
    const [addNewKRAFormData, setAddNewKRAFormData] = useState({
        department: null,
        financialYear: null,
        kraName: '',
        kraRatingType:"Rating",
        ratingDuration:"Quarterly",
        bonusDuration:"Bonus Duration",
        isActive:false
    })

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e.target ? e.target : { value: e };

        setAddNewKRAFormData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };

            return updatedState;
        });
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));

    };



    const submitKRA = async () => {
        const validationResponse = await validateSchema(addNewKRA, addNewKRAFormData);
        if (validationResponse.isValid) {
            const {
                department,
                financialYear,
                kraName,
                kraRatingType,
                ratingDuration,
                bonusDuration,
                isActive
            }=addNewKRAFormData

            const payload={
                kra_name:kraName,
                kra_rating_type:kraRatingType,
                rating_duration:ratingDuration,
                bonus_duration:bonusDuration,
                financial_id:financialYear?.value,
                department:department?.value,
                is_active:isActive
            }
            handleCreateUpdateKra(payload)
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        return (() => {
            // setSelectedRatingDurationEmp('')
        })
    }, [openModel])
    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    // width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">
                        {t("headings.addNewKRA")}
                    </h5>
                    <button type="button"
                        className="btn-close"
                        data-bs-dismiss="offcanvas"
                        aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />
                </div>


                <div className="offcanvas-body myRequest ">
                    <div className="formPanel">
                        <div className="formPanel">
                            <div className="form-group mb-3">
                                <Label text={t("labels.financialYear")} required={true} />
                                <CustomSelect
                                    data={financialYearListData}
                                    placeholder={t("placeholders.financialYear")}
                                    value={addNewKRAFormData?.financialYear || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "financialYear")}
                                    name="financialYear"
                                />
                                {errors?.financialYear && (
                                    <CustomError text={errors.financialYear} />
                                )}
                            </div>
                            <div className="form-group mb-3">
                                <Label text={t("labels.kraName")} required={true} />
                                <Input
                                    type="text"
                                    placeholder={t("placeholders.kraName")}
                                    value={addNewKRAFormData?.kraName}
                                    name="kraName"
                                    onChange={(e) => handleFromChange(e)}
                                />
                                {errors?.kraName && (
                                    <CustomError text={errors.kraName} />
                                )}

                            </div>
                            <div className="form-group mb-3">
                                <Label text={t("labels.selectDepartment")} required={true} />
                                <CustomSelect
                                    data={allDepartmentList}
                                    placeholder={t("placeholders.selectDepartment")}
                                    value={addNewKRAFormData?.department || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "department")}
                                    name="department"
                                />
                                {errors?.department && (
                                    <CustomError text={errors.department} />
                                )}

                            </div>
                            <div className="d-flex justify-content-between align-items-center typerating">
                                <div>
                                    <span>
                                        KRA Rating Type <strong>{addNewKRAFormData?.kraRatingType}</strong>
                                    </span>
                                </div>
                                <div>
                                    <span>
                                        Rating Duration <strong>{addNewKRAFormData?.ratingDuration}</strong>
                                    </span>
                                </div>
                                <div>
                                    <span>
                                        Bonus Duration <strong>{addNewKRAFormData?.bonusDuration}</strong>
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.isActive")} />
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <CustCheckbox
                                        key={"is_active"}
                                        id={`is_active`}
                                        checked={addNewKRAFormData?.isActive}
                                        // handleChange={(e) => setIsActive(!isActive)}
                                        name={"is_active"}  
                                    />
                                </div>
                            </div>
                        </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={t("btns.submit")}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitKRA()}
                        //  isLoading={ addPositionPending}
                        />
                    </div>
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default AddNewKRA
