import React, { useState } from 'react'
import { CustCheckbox, Label } from '../../../commanComponent'
import CustomSelect from '../../../commanComponent/CustomSelect'
import { useCustomTranslation } from '../../../../translation'
import { CustomButton } from '../../../globalComponents'

const AssignKRAModal = (props) => {
    const { t } = useCustomTranslation()
    const {
        openModel,
        handleOpenClose,
        selectedAssignKRA

    } = props
    const [assignBy, setAssignBy] = useState(null)

    const approvalForDropDown = [
        { value: "designation", label: "Designation" },
        { value: "employee", label: "Employee" },
    ]
    const renderEmployeeAssignKRA = () => {
        return (<form>
            <div className="mb-2 form-check">

                <input type="checkbox" className="form-check-input" id="test1" />
                <label className="form-check-label" htmlFor="test1">
                    text@qdegrees.com
                </label>
            </div>
            <div className="mb-2 form-check">
                <input type="checkbox" className="form-check-input" id="text2" />
                <label className="form-check-label" htmlFor="text2">
                    text1@qdegrees.com
                </label>
            </div>
        </form>)
    }
    const renderDesignationAssignKRA = () => {
        return (
            <form>
                <div className="mb-2 form-check">
                    <input
                        type="checkbox"
                        className="form-check-input"
                        id="exampleCheck1"
                    />
                    <label className="form-check-label" htmlFor="exampleCheck1">
                        Product Development-Intern
                    </label>
                </div>
                <div className="mb-2 form-check">
                    <input
                        type="checkbox"
                        className="form-check-input"
                        id="exampleCheck2"
                    />
                    <label className="form-check-label" htmlFor="exampleCheck2">
                        Seniar Node Js Developer
                    </label>
                </div>
            </form>
        )
    }

    return (
        <div>
            <div className="modal fade attendanceViewodal"
                id="assignKra"
                style={
                    {
                        display: openModel ? "unset !important" : "none !important",
                        opacity: openModel ? "unset" : "0"
                    }
                }
            >
                <div className="modal-dialog modal-dialog-centered">
                    <div className="modal-content rounded-0 border-0">
                        <div className="modal-header border-0">
                            <h1 className="modal-title fs-5 fw-bold text-primary">Assign KRA</h1>
                            <button
                                type="button"
                                className="btn-close"
                                data-bs-dismiss="modal"
                                aria-label="Close"
                                onClick={() => handleOpenClose()}
                            />
                        </div>
                        <div className="modal-body ">
                            <div className="d-flex justify-content-between align-items-center assignmain mb-4">
                                <div className="">
                                    <span>KRA ID</span>
                                    <strong>665</strong>
                                </div>
                                <div className="">
                                    <span>KRA Name</span>
                                    <strong>selectedAssignKRA</strong>
                                </div>
                                <div className="">
                                    <span>Department</span>
                                    <strong>Product Development</strong>
                                </div>
                            </div>
                            <div className="approveSlct mb-3">
                                <Label text={"Approve For"} required={true} />
                                <CustomSelect
                                    data={approvalForDropDown || []}
                                    placeholder={t("placeholders.assignBy")}
                                    value={assignBy}
                                    handleChange={(selectedOption) => setAssignBy(selectedOption)}
                                    name="assignBy"
                                />

                            </div>
                            {assignBy?.value === "designation" ? <div className="designationKra">
                                <p>Select Designation for Assign KRA</p>
                                {renderDesignationAssignKRA()}
                            </div>
                                : <div className="designationKra"  >
                                    <p>Select Employee for Assign KRA</p>
                                    {renderEmployeeAssignKRA()}
                                </div>}
                            <div className="py-2">
                                <CustomButton
                                    classNames="btn btn-primary gradientBg mt-2"
                                    text={"Assign KRA"}
                                />
                            </div>
                        </div>
                    </div>
                </div>

            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}

        </div>
    )
}

export default AssignKRAModal
