import React, { useMemo, useState } from 'react'
import { Label } from 'reactstrap'
import { CustomTable } from '../../../reuseableComponents'
import { Icon } from '../../../commanComponent'
import { setStatusColor } from '../../../../utils/common'
import { CustomButton } from '../../../globalComponents'
import CustomSelect from '../../../commanComponent/CustomSelect'
import { useCustomTranslation } from '../../../../translation'

const CreatedKRATable = (props) => {
    const { t } = useCustomTranslation()
    const { createdKRAData, currentPage, setCurrentPage, toggleAssignKRA, toggleViewKRAParameter,financialYearListData } = props
    const [financialYear, setFinancialYear] = useState('')
    const createdKRAField = [
        { key: 'sn', label: 'S.N' },
        { key: 'kra', label: 'KRA' },
        { key: 'department', label: 'Department' },
        { key: 'ratingType', label: 'Rating Type' },
        { key: 'status', label: 'Status' },
        { key: 'assignKRA', label: 'Assign KRA' },
        { key: 'action', label: 'Action' },
    ]

    const createdKRAScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        status: (item, index) => (
            <span
                key={index}
                className={`${setStatusColor(item?.status)} text-capitalize`}
            >
                {item?.status}
            </span>
        ),
        assignKRA: (item, index) => (
            <span key={index}>
                <CustomButton
                    text={"Assign KRA"}
                    classNames="btn btn-primary gradientBg"
                    handleClick={() => toggleAssignKRA(item)}
                />
            </span>
        ),
        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                    <li>
                        <span className="dropdown-item"
                            onClick={() => toggleViewKRAParameter(item)}
                        >
                            <Icon className={"fa fa-eye"} /> View</span>
                    </li>

                    <li>
                        <span className="dropdown-item"
                        //   onClick={() => toggleUpdateRatingDurationModal(item)}
                        >
                            <Icon className={"fa fa-user-plus"} /> Ready to Assign</span>
                    </li>
                    <li>
                        <span className="dropdown-item"
                        //   onClick={() => toggleUpdateRatingDurationModal(item)}
                        >
                            <Icon className={"fa fa-plus"} /> Add KPI</span>
                    </li>
                </ul>
            </div>

        ),

    }), [createdKRAData]);
    return (
        <div>
            <div className="cardBox myRequest">
                <div className="cardHead d-flex justify-content-between mb-3">
                    <div className="d-flex align-items-center">
                        <img
                            src="images/team-rating.svg"
                            alt=""
                            width={40}
                            className="me-2 fw-bold"
                        />
                        <div className="">
                            <h3> Manage KRA</h3>
                            <p>To assign any KRA total KPI weight need to be 100%?</p>
                        </div>
                    </div>
                    <div className="w-25">
                        <CustomSelect
                            data={financialYearListData}
                            placeholder={t("placeholders.financialYear")}
                            value={financialYear || ''}
                            handleChange={(selectedOption) => setFinancialYear(selectedOption)}
                            name="department"
                        />
                    </div>
                </div>
                <div className="table-responsive tableDiv">
                    <CustomTable
                        data={createdKRAData}
                        fields={createdKRAField}
                        // isLoading={isLoading}
                        pagination={true}
                        currentPage={currentPage}
                        setCurrentPage={setCurrentPage}
                        totalPages={createdKRAData?.totalPages}
                        isHeader={false}
                        tableStyle={{ padding: "0px", boxShadow: "none" }}
                        scopedSlots={createdKRAScopedSlots}
                    />

                </div>
            </div>

        </div>
    )
}

export default CreatedKRATable
